body {
    font-family: Arial, Helvetica, sans-serif;
    margin: 0;
    padding: 0; }

.parallaxmotion {

    overflow-x: hidden;
    overflow-y: auto;

    &-img {
        transform: translateZ(0);
        position: fixed;
        top: 0;
        left: 0;

        background-size: cover;
        background-position: 50%; } }

.loading {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;

    background: black;
    color: white;

    display: flex;
    align-items: center;
    justify-content: center; }

.motion-label {
    opacity: 0;
    position: fixed;

    transition: opacity 1s;
    color: white;

    transform: translateX(-50%);

    &.show {
        opacity: 1; } }

h1 {
    font-size: 15em; }
